<template>
<div id="cailiao">
    <el-button type="success" size="small" class="add" @click="add">添加材料</el-button>
    <el-table :data="tableData" style="width:52.1%" :row-class-name="tableRowClassName" v-loading="load">
        <el-table-column prop="businessName" label="业务类型" width="180"></el-table-column>
        <el-table-column prop="materialsNo" label="材料编号" width="180"></el-table-column>
        <el-table-column prop="materialsName" label="材料名称" width="180"></el-table-column><el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
                <el-button type="text" @click="modifyMes(scope.row)" size="small">修改</el-button>
                <el-button type="text" @click="deleteMa(scope.row)" size="small">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-dialog :visible.sync="modify" :title="dialogname"  append-to-body width="40%">
        <el-form :model="windowsMes" ref="windowsMes" label-width="20%" :rules="rule">
            <el-form-item label="业务类型" prop="businessId">
                <el-select v-model="windowsMes.businessId" placeholder="选择业务类型" style="position:absolute;left:97px;">
                    <el-option v-for="item in business" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="材料编号" prop="materialsNo">
                <el-input v-model="windowsMes.materialsNo"></el-input>
            </el-form-item>
            <el-form-item label="材料名称" prop="materialsName">
                <el-input v-model="windowsMes.materialsName"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="modify=false" >取消</el-button>
            <el-button type="primary" @click="commit">确认</el-button>
        </div>
    </el-dialog>
</div>
</template>
<script>
export default {
    data(){
        return{
            tableData:[],
            modify:false,
            business:[],
            type: '0',
            load:true,
            dialogname:'修改材料信息',
            windowsMes:
                {businessId :'',materialsNo:'',materialsName:''},
            rule:{
                businessName:[{required:true}],
                materialsNo:[{required:true}],
                materialsName:[{required:true}]
            }
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        init(){
            this.axios.getmaterials({}).then((res)=>{
                this.load=false
                this.tableData =res.dataList
            })
            this.business = []
            this.axios.getBusiness({}).then((res)=>{
                for(let index in res.dataList){
                    let data ={}
                    for(let key in res.dataList[index]){
                        if(key == 'businessName')
                            data.label = res.dataList[index].businessName
                        else{
                            data.value = res.dataList[index].id
                        }
                    }
                    this.business.push(data)
                }
            })
        },
        add(){
            this.dialogname = '添加材料信息'
            this.modify = true
            this.type = '1'
            this.windowsMes ={}
        },
        deleteMa(row){
            this.axios.deletematerials({'id':row.id}).then((res)=>{
                if(res.success){
                    this.$message.success(res.message)
                    this.modify =false
                    this.init()
                }
                else{
                    this.$message.error(res.message)
                }
            })
        },
        modifyMes(row){
            this.modify=true
            this.type='0'
            this.dialogname = '修改材料信息'
            this.axios.getmaterials({'id':row.id}).then((res)=>{
                this.windowsMes = res.dataList[0]
            })
        },
        commit(){
            this.$refs["windowsMes"].validate((valid)=>{
                if(valid){
                    if(this.type == 0){
                        this.axios.modifymaterials(this.windowsMes).then((res)=>{
                            if(res.success){
                                this.init()
                                this.modify = false
                            }
                            else{
                                this.$message.error(res.message)
                            }
                        })
                    }
                    else{
                        this.axios.addmaterials(this.windowsMes).then((res)=>{
                            if(res.success){
                                this.init()
                                this.modify = false
                            }
                            else{
                                this.$message.error(res.message)
                            }
                        })
                    }
                }
                else
                    this.$message.error('表单有误')
            })
        },
        tableRowClassName({row, rowIndex}) {
            if (rowIndex === 1) {
                return 'warning-row';
            } else if (rowIndex === 3) {
                return 'success-row';
            }
            return '';
        }
    }
}
</script>
<style lang="scss">
#cailiao{
    .add{
        position: relative;
        top: 100px;
        left: 237px;
    }
    .el-table{
        border:1px solid #000;
        border-radius: 7px 7px 7px 7px;
        box-shadow:0px 0px 3px #000;
        position: relative;
        top:100px;
        left: 250px;
    }
    .el-table .warning-row {
        background: oldlace;
    }
    .el-table .success-row {
        background: #f0f9eb;
    }
}
</style>


